            }

            &:active {
                background-color: hsl(192, 100%, 29%);
            }
        }

        &:nth-child(3) {
            background-color: hsl(192, 100%, 44%);

            &:hover {
                background-color: hsl(192, 100%, 34%);
            }

            &:active {
                background-color: hsl(192, 100%, 29%);
            }
        }

    }
}

.modal[data-state="form"][open] {
    form:nth-child(1) {
        display: grid;
        padding: 1rem 2rem 4rem 2rem;

        & input,
        textarea {
            margin-top: 0.4rem;
            margin-bottom: 1rem;
        }

        & input[type="checkbox"] {
            transform: scale(1.4);
        }
    }

    div:nth-child(2) {
        display: grid;
        grid-template-columns: repeat(3, min-content);
        gap: 0.5rem;
