___CSS_LOADER_EXPORT___.push([module.id, `.project-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
    background-color: #5A5550;
    padding: 5rem 3rem 3rem 3rem;
    gap: 2rem;
    position: relative;
    overflow-y: auto;
    scrollbar-color: grey #5A5550;
    height:calc(100vh - 5rem - 3rem);
    max-height: calc(100vh - 5rem - 3rem);
}

.todo-card {
    display: flex;
    flex-direction: column;

    aspect-ratio: 1/1;
    background-color: #CDBEAF;
    border: 5px solid #B39F8B;
    border-radius: 1rem;


    /*header*/
    &>div:first-of-type {
        background-color: #B39F8B;
        /* margin: 0 -1.5rem 0 -1.5rem;
        border-radius: 1rem 1rem 0 0; */
        padding: 0.2rem 0 0.2rem 0.5rem;
        font-weight: bold;
        &>.delete-icon{
            vertical-align: middle;
            float: right;
        }
    }

    &>div:nth-of-type(2) {
        padding: 0 1.5rem 1.5rem 1.5rem;
        row-gap: 0.5rem;

        & h3 {
            font-size: 2rem;
        }

        & .date {
            color: #A20226;
            font-weight: bold;
        }

        & ul {
            list-style: none;
        }

        & li input{
            margin-right: 0.5rem;
            vertical-align: middle;
        }

    }


}

.delete-icon{
    margin-right: 0.5rem;
}
.delete-icon,
.edit-icon {
    width: 1rem;
    height: 1rem;
    align-self: center;
    justify-self: end;
}

.title-box,
.dueDate-box{
    display: grid;
    width: 100%;
    grid-template-columns: auto min-content;
}

.description-box,
.notes-box,
.checklist-box {
    display: grid;
    width: 100%;
    grid-template-columns: auto min-content min-content;
    margin-top: 1rem;
    grid-template-rows: auto auto;
}

.description-box>p::first-letter,
.notes-box>p::first-letter {
    padding-left: 1rem;
}

.description, .notes{
    white-space: pre;
}

.card-buttons{
    margin-top: 0.5rem;
    display: grid;
    grid-template-rows: repeat(3,min-content);
    gap: 0.2rem;
    & button{
        white-space: nowrap;
        justify-self: start;
        padding: 0.4rem 0.6rem;
        color: white;
        border: 1px solid black;
        border-radius: 0.3rem;
        background-color: hsl(30, 21%, 55%);
        &:hover{
            background-color: hsl(30, 21%, 65%);
        }
        &:active{
            background-color: hsl(30, 21%, 70%);
        }
    }
}`, "",{"version":3,"sources":["webpack://./src/todos.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,4DAA4D;IAC5D,yBAAyB;IACzB,4BAA4B;IAC5B,SAAS;IACT,kBAAkB;IAClB,gBAAgB;IAChB,6BAA6B;IAC7B,gCAAgC;IAChC,qCAAqC;AACzC;;AAEA;IACI,aAAa;IACb,sBAAsB;;IAEtB,iBAAiB;IACjB,yBAAyB;IACzB,yBAAyB;IACzB,mBAAmB;;;IAGnB,SAAS;IACT;QACI,yBAAyB;QACzB;uCAC+B;QAC/B,+BAA+B;QAC/B,iBAAiB;QACjB;YACI,sBAAsB;YACtB,YAAY;QAChB;IACJ;;IAEA;QACI,+BAA+B;QAC/B,eAAe;;QAEf;YACI,eAAe;QACnB;;QAEA;YACI,cAAc;YACd,iBAAiB;QACrB;;QAEA;YACI,gBAAgB;QACpB;;QAEA;YACI,oBAAoB;YACpB,sBAAsB;QAC1B;;IAEJ;;;AAGJ;;AAEA;IACI,oBAAoB;AACxB;AACA;;IAEI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;;IAEI,aAAa;IACb,WAAW;IACX,uCAAuC;AAC3C;;AAEA;;;IAGI,aAAa;IACb,WAAW;IACX,mDAAmD;IACnD,gBAAgB;IAChB,6BAA6B;AACjC;;AAEA;;IAEI,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,yCAAyC;IACzC,WAAW;IACX;QACI,mBAAmB;QACnB,mBAAmB;QACnB,sBAAsB;QACtB,YAAY;QACZ,uBAAuB;QACvB,qBAAqB;QACrB,mCAAmC;QACnC;YACI,mCAAmC;QACvC;QACA;YACI,mCAAmC;QACvC;IACJ;AACJ","sourcesContent":[".project-container {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));\n    background-color: #5A5550;\n    padding: 5rem 3rem 3rem 3rem;\n    gap: 2rem;\n    position: relative;\n    overflow-y: auto;\n    scrollbar-color: grey #5A5550;\n    height:calc(100vh - 5rem - 3rem);\n    max-height: calc(100vh - 5rem - 3rem);\n}\n\n.todo-card {\n    display: flex;\n    flex-direction: column;\n\n    aspect-ratio: 1/1;\n    background-color: #CDBEAF;\n    border: 5px solid #B39F8B;\n    border-radius: 1rem;\n\n\n    /*header*/\n    &>div:first-of-type {\n        background-color: #B39F8B;\n        /* margin: 0 -1.5rem 0 -1.5rem;\n        border-radius: 1rem 1rem 0 0; */\n        padding: 0.2rem 0 0.2rem 0.5rem;\n        font-weight: bold;\n        &>.delete-icon{\n            vertical-align: middle;\n            float: right;\n        }\n    }\n\n    &>div:nth-of-type(2) {\n        padding: 0 1.5rem 1.5rem 1.5rem;\n        