___CSS_LOADER_EXPORT___.push([module.id, `* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    height: 100vh;
    display: grid;
    grid-template-columns: 12rem auto;
    grid-template-rows: 5rem calc(100vh - 5rem - 3rem) 3rem;
    grid-template-areas: "header header"
        "sidebar container"
        "footer footer";
}

.header {
    grid-area: header;
    background-color: hsl(30, 23%, 75%); /*#CDBEAF*/
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    & h1{
        font-size: 2.5rem;
        color: hsl(30, 23%, 60%);
        text-shadow: -2px -2px 2px rgba(255,255,255,.1), 2px 2px 2px rgba(0,0,0,.5);
    }

    & div {
        flex: 0 1 auto;
        display: flex;
        gap: 1rem;
    }

    & button {
        flex: 0 1 auto;
        padding: 0.5rem 1rem;
        border-radius: 1rem;
        font-size: 1.2rem;
        background-color: #CDBEAF;
    }
}

.sidebar {
    grid-area: sidebar;
    background-color: #E6DCDC;
    scrollbar-color: grey #E6DCDC;
    overflow-y: auto;
    padding: 0.5rem;
    font-size: 1.7rem;

    &>h2 {
        padding-left: 0.5rem;
    }

    &>ul {
        margin-top: 1rem;
        list-style: none;

        &>li {
            font-size: 1.3rem;
            font-style: italic;
            font-weight: bold;
            border: 2px solid #E6DCDC;
            border-radius: 1rem;
            padding: 0.5rem;

            &::before {
                content: "#";
                color: #A20226;
                margin-right: 0.5rem;
            }

            &:hover {
                border: 2px outset #E6DCDC;
                cursor: pointer;
            }

            &:active {
                border: 2px inset #E6DCDC;
            }
        }

        & .focus-project::before {
            content: "\\25BA";
        }
    }
}

.container {
    grid-area: container;
    background-color: #5A5550;
    /* overflow-y: auto;
    scrollbar-color: grey aquamarine; */
}

.footer {
    grid-area: footer;
    background-color: #CDBEAF;
}

.project-btn-container {
    position: absolute;
    right: 0.5rem;
    top: 1rem
}

.add-card-icon {
    color: white;
    width: 3rem;
    height: 3rem;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;IACT,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,aAAa;IACb,iCAAiC;IACjC,uDAAuD;IACvD;;uBAEmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,mCAAmC,EAAE,UAAU;IAC/C,aAAa;IACb,8BAA8B;IAC9B,aAAa;IACb;QACI,iBAAiB;QACjB,wBAAwB;QACxB,2EAA2E;IAC/E;;IAEA;QACI,cAAc;QACd,aAAa;QACb,SAAS;IACb;;IAEA;QACI,cAAc;QACd,oBAAoB;QACpB,mBAAmB;QACnB,iBAAiB;QACjB,yBAAyB;IAC7B;AACJ;;AAEA;IACI,kBAAkB;IAClB,yBAAyB;IACzB,6BAA6B;IAC7B,gBAAgB;IAChB,eAAe;IACf,iBAAiB;;IAEjB;QACI,oBAAoB;IACxB;;IAEA;QACI,gBAAgB;QAChB,gBAAgB;;QAEhB;YACI,iBAAiB;YACjB,kBAAkB;YAClB,iBAAiB;YACjB,yBAAyB;YACzB,mBAAmB;YACnB,eAAe;;YAEf;gBACI,YAAY;gBACZ,cAAc;gBACd,oBAAoB;YACxB;;YAEA;gBACI,0BAA0B;gBAC1B,eAAe;YACnB;;YAEA;gBACI,yBAAyB;YAC7B;QACJ;;QAEA;YACI,gBAAgB;QACpB;IACJ;AACJ;;AAEA;IACI,oBAAoB;IACpB,yBAAyB;IACzB;uCACmC;AACvC;;AAEA;IACI,iBAAiB;IACjB,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb;AACJ;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,YAAY;AAChB","sourcesContent":["* {\n    padding: 0;\n    margin: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    height: 100vh;\n    display: grid;\n    grid-template-columns: 12rem auto;\n    grid-template-rows: 5rem calc(100vh - 5rem - 3rem) 3rem;\n    grid-template-areas: \"header header\"\n        \"sidebar container\"\n        \"footer footer\";\n}\n\n.header {\n    grid-area: header;\n    background-color: hsl(30, 23%, 75%); /*#CDBEAF*/\n    display: flex;\n    justify-content: space-between;\n    padding: 1rem;\n    & h1{\n        font-size: 2.5rem;\n        color: hsl(30, 23%, 60%);\n        text-shadow: -2px -2px 2px rgba(255,255,255,.1), 2px 2px 2px rgba(0,0,0,.5);\n    }\n\n    & div {\n        flex: 0 1 auto;\n        display: flex;\n        gap: 1rem;\n    }\n\n    & button {\n        flex: 0 1 auto;\n        padding: 0.5rem 1rem;\n        border-radius: 1rem;\n        font-size: 1.2rem;\n        background-color: #CDBEAF;\n    }\n}\n\n.sidebar {\n    grid-area: sidebar;\n    background-color: #E6DCDC;\n   